////////////////////////////////////////////////

let socket = io();
// ÂàóË°®listÔºåËæìÂÖ•Ê°ÜcontentÔºåÊåâÈíÆsendBtn
let list = document.getElementById("list"),
  input = document.getElementById("input"),
  sendBtn = document.getElementById("sendBtn");

let userId = "";

// ÂèëË®ÄÁöÑÊñπÊ≥ï
function send() {
  console.log(socket.connected);
  let value = input.value;
  if (value) {
    // ÂèëÈÄÅÊ∂àÊÅØÁªôÊúçÂä°Âô®
    socket.emit("message", value);
    input.value = "";
  } else {
    alert("ËæìÂÖ•ÁöÑÂÜÖÂÆπ‰∏çËÉΩ‰∏∫Á©∫ÔºÅ");
  }
}
// ÂõûËΩ¶ÂèëË®ÄÁöÑÊñπÊ≥ï
function keySend(event) {
  const key = event.keyCode;
  if (key === 13) {
    send();
  }
}
// ÁßÅËÅäÁöÑÊñπÊ≥ï
function privateChat(event) {
  let target = event.target;
  let user = target.innerText;
  if (target.className === "user") {
    input.value = `@${user} `;
  }
}
// ËøõÂÖ•ÊàøÈó¥(Áæ§)
function join(room) {
  socket.emit("join", room);
}
// Á¶ªÂºÄÊàøÈó¥(Áæ§)
function leave(room) {
  socket.emit("leave", room);
}

// ÁÇπÂáªÊåâÈíÆËøõË°åÂèëË®Ä
sendBtn.onclick = send;
// ÊåâÂõûËΩ¶ËøõË°åÂèëË®Ä
input.onkeydown = function (event) {
  keySend(event);
};
// Ê∑ªÂä†ÁßÅËÅä
list.onclick = function (event) {
  privateChat(event);
};
// ÁõëÂê¨ËøõÂÖ•ÊàøÈó¥ÂêéÔºåÂ∞ÜËøõÂÖ•ÊàøÈó¥ÊåâÈíÆÈöêËóè
socket.on("joined", (room) => {
  document.getElementById(`join-${room}`).style.display = "none";
  document.getElementById(`leave-${room}`).style.display = "inline-block";
});
// ÁõëÂê¨Á¶ªÂºÄÊàøÈó¥ÂêéÔºåÂ∞ÜÁ¶ªÂºÄÊàøÈó¥ÊåâÈíÆÈöêËóè
socket.on("leaved", (room) => {
  document.getElementById(`join-${room}`).style.display = "inline-block";
  document.getElementById(`leave-${room}`).style.display = "none";
});

// ÁõëÂê¨‰∏éÊúçÂä°Á´ØÁöÑËøûÊé•
socket.on("connect", () => {
  console.log("ËøûÊé•ÊàêÂäü");
  socket.emit("getHistory");
});
// Êé•Êî∂ÂéÜÂè≤Ê∂àÊÅØ
socket.on("history", (history) => {
  // historyÊãøÂà∞ÁöÑÊòØ‰∏Ä‰∏™Êï∞ÁªÑÔºåÊâÄ‰ª•Áî®mapÊò†Â∞ÑÊàêÊñ∞Êï∞ÁªÑÔºåÁÑ∂ÂêéÂÜçjoin‰∏Ä‰∏ãËøûÊé•ÊãºÊàêÂ≠óÁ¨¶‰∏≤
  let html = history
    .map((data) => {
      return `<li class="list-group-item">
                            <p style="color: #ccc;"><span class="user" style="color:${data.color}">${data.user} </span>${data.createAt}</p>
                            <p class="content" style="background-color: ${data.color}">${data.content}</p>
                        </li>`;
    })
    .join("");
  list.innerHTML =
    html + '<li style="margin: 16px 0;text-align: center">‰ª•‰∏äÊòØÂéÜÂè≤Ê∂àÊÅØ</li>';
  // Â∞ÜËÅäÂ§©Âå∫ÂüüÁöÑÊªöÂä®Êù°ËÆæÁΩÆÂà∞ÊúÄÊñ∞ÂÜÖÂÆπÁöÑ‰ΩçÁΩÆ
  list.scrollTop = list.scrollHeight;
});
// Êé•Êî∂ÊúçÂä°Á´Ø‰º†ËøáÊù•ÁöÑÊ∂àÊÅØ
socket.on("message", (data) => {
  console.log(data);
  console.log("userId", userId);
  let li = document.createElement("li");
  li.className = "list-group-item";
  // Â¶ÇÊûúÁî®Êà∑id‰∏é‰º†ËøáÊù•ÁöÑidÁõ∏ÂêåÂ∞±Ë°®Á§∫ÊòØËá™Â∑±
  li.style.textAlign = userId === data.id ? "right" : "left";
  li.innerHTML = `<p style="color: #ccc;"><span class="user" style="color:${data.color}">${data.user} </span>${data.createAt}</p>
                    <p class="content" style="background-color: ${data.color}">${data.content}</p>`;
  list.appendChild(li);
  // Â∞ÜËÅäÂ§©Âå∫ÂüüÁöÑÊªöÂä®Êù°ËÆæÁΩÆÂà∞ÊúÄÊñ∞ÂÜÖÂÆπÁöÑ‰ΩçÁΩÆ
  list.scrollTop = list.scrollHeight;
});
// Ëé∑ÂèñÂØπÂ∫îÁöÑÁî®Êà∑id
socket.on("getId", (id) => {
  userId = id;
});
